import React, { Component } from 'react';
import Helper from '../../component/Helper'
import ReactHtmlParser from 'react-html-parser';
import Footer from '../../component/Footer'
export default class Impressum extends Component{

    componentDidMount(){
        this.props.setPath("/privacypolicy")
    }

    render(){
        return(
            <div>
                 <div className="container-fluid" style={{}}>
                  <div className="mt-3 pt-3 p-5 ">
                    <h3 className=""><span className="headlines">{Helper.translate("PRIVACY POLICY", this.props.lang)}</span></h3>
                    <div className="pt-5">
                        {ReactHtmlParser(Helper.translate('<h4>1. An overview of data protection</h4><h5>General information</h5> <p>The following information will provide you with an easy to navigate overview of what will happen with your personal data when you visit our website. The term “personal data” comprises all data that can be used to personally identify you. For detailed information about the subject matter of data protection, please consult our Data Protection Declaration, which we have included beneath this copy.</p><h5>Data recording on our website</h5> <p><strong>Who is the responsible party for the recording of data on this website (i.e. the “controller”)?</strong></p> <p>The data on this website is processed by the operator of the website, whose contact information is available under section “Information Required by Law” on this website.</p> <p><strong>How do we record your data?</strong></p> <p>We collect your data as a result of your sharing of your data with us. This may, for instance be information you enter into our contact form.</p> <p>Our IT systems automatically record other data when you visit our website. This data comprises primarily technical information (e.g. web browser, operating system or time the site was accessed). This information is recorded automatically when you access our website.</p> <p><strong>What are the purposes we use your data for?</strong></p> <p>A portion of the information is generated to guarantee the error free provision of the website. Other data may be used to analyse your user patterns.</p> <p><strong>What rights do you have as far as your information is concerned?</strong></p> <p>You have the right to receive information about the source, recipients and purposes of your archived personal data at any time without having to pay a fee for such disclosures. You also have the right to demand that your data are rectified, blocked or eradicated. Please do not hesitate to contact us at any time under the address disclosed in section “Information Required by Law” on this website if you have questions about this or any other data protection related issues. You also have the right to log a complaint with the competent supervising agency.</p> <p>Moreover, under certain circumstances, you have the right to demand the restriction of the processing of your personal data. For details, please consult the Data Protection Declaration under section “Right to Restriction of Data Processing.”</p><h4>2. General information and mandatory information</h4><h5>Data protection</h5> <p>The operators of this website and its pages take the protection of your personal data very seriously. Hence, we handle your personal data as confidential information and in compliance with the statutory data protection regulations and this Data Protection Declaration.</p> <p>Whenever you use this website, a variety of personal information will be collected. Personal data comprises data that can be used to personally identify you. This Data Protection Declaration explains which data we collect as well as the purposes we use this data for. It also explains how, and for which purpose the information is collected.</p> <p>We herewith advise you that the transmission of data via the Internet (i.e. through e-mail communications) may be prone to security gaps. It is not possible to completely protect data against third party access.</p><h5>Information about the responsible party (referred to as the “controller” in the GDPR)</h5> <p>The data processing controller on this website is:</p> <p>Kevin Herlt<br />Sandweg 2e<br />21509 Glinde<br />Deutschland</p><p>Phone: +49 17620389762<br />E-mail: kevin.herlt1998@gmail.com</p><p>The controller is the natural person or legal entity that single-handedly or jointly with others makes decisions as to the purposes of and resources for the processing of personal data (e.g. names, e-mail addresses, etc.).</p><h5>Revocation of your consent to the processing of data</h5> <p>A wide range of data processing transactions are possible only subject to your express consent. You can also revoke at any time any consent you have already given us. To do so, all you are required to do is sent us an informal notification via e-mail. This shall be without prejudice to the lawfulness of any data collection that occurred prior to your revocation.</p><h5>Right to object to the collection of data in special cases; right to object to direct advertising (Art. 21 GDPR)</h5> <p><strong>In the event that data are processed on the basis of Art. 6 Sect. 1 lit. e or f GDPR, you have the right to at any time object to the processing of your personal data based on grounds arising from your unique situation. This also applies to any profiling based on these provisions. To determine the legal basis, on which any processing of data is based, please consult this Data Protection Declaration. If you log an objection, we will no longer process your affected personal data, unless we are in a position to present compelling protection worthy grounds for the processing of your data, that outweigh your interests, rights and freedoms or if the purpose of the processing is the claiming, exercising or defence of legal entitlements (objection pursuant to Art. 21 Sect. 1 GDPR).</strong></p> <p><strong>If your personal data is being processed in order to engage in direct advertising, you have the right to at any time object to the processing of your affected personal data for the purposes of such advertising. This also applies to profiling to the extent that it is affiliated with such direct advertising. If you object, your personal data will subsequently no longer be used for direct advertising purposes (objection pursuant to Art. 21 Sect. 2 GDPR).</strong></p><h5>Right to log a complaint with the competent supervisory agency</h5> <p>In the event of violations of the GDPR, data subjects are entitled to log a complaint with a supervisory agency, in particular in the member state where they usually maintain their domicile, place of work or at the place where the alleged violation occurred. The right to log a complaint is in effect regardless of any other administrative or court proceedings available as legal recourses.</p><h5>Right to data portability</h5> <p>You have the right to demand that we hand over any data we automatically process on the basis of your consent or in order to fulfil a contract be handed over to you or a third party in a commonly used, machine readable format. If you should demand the direct transfer of the data to another controller, this will be done only if it is technically feasible.</p><h5>SSL and/or TLS encryption</h5> <p>For security reasons and to protect the transmission of confidential content, such as purchase orders or inquiries you submit to us as the website operator, this website uses either an SSL or a TLS encryption programme. You can recognise an encrypted connection by checking whether the address line of the browser switches from “http://” to “https://” and also by the appearance of the lock icon in the browser line.</p> <p>If the SSL or TLS encryption is activated, data you transmit to us cannot be read by third parties.</p><h5>Information about, blockage, rectification and eradication of data</h5> <p>Within the scope of the applicable statutory provisions, you have the right to at any time demand information about your archived personal data, their source and recipients as well as the purpose of the processing of your data. You may also have a right to have your data rectified, blocked or eradicated. If you have questions about this subject matter or any other questions about personal data, please do not hesitate to contact us at any time at the address provided in section “Information Required by Law.”</p><h5>Right to demand processing restrictions</h5> <p>You have the right to demand the imposition of restrictions as far as the processing of your personal data is concerned. To do so, you may contact us at any time at the address provided in section “Information Required by Law.” The right to demand restriction of processing applies in the following cases:</p> <ul> <li>In the event that you should dispute the correctness of your data archived by us, we will usually need some time to verify this claim. During the time that this investigation is ongoing, you have the right to demand that we restrict the processing of your personal data.</li> <li>If the processing of your personal data was/is conducted in an unlawful manner, you have the option to demand the restriction of the processing of your data in lieu of demanding the eradication of this data.</li> <li>If we do not need your personal data any longer and you need it to exercise, defend or claim legal entitlements, you have the right to demand the restriction of the processing of your personal data instead of its eradication.</li> <li>If you have raised an objection pursuant to Art. 21 Sect. 1 GDPR, your rights and our rights will have to be weighed against each other. As long as it has not been determined whose interests prevail, you have the right to demand a restriction of the processing of your personal data.</li> </ul> <p>If you have restricted the processing of your personal data, these data – with the exception of their archiving – may be processed only subject to your consent or to claim, exercise or defend legal entitlements or to protect the rights of other natural persons or legal entities or for important public interest reasons cited by the European Union or a member state of the EU.</p><h4>3. Recording of data on our website</h4><h5>Cookies</h5> <p>In some instances, our website and its pages use so-called cookies. Cookies do not cause any damage to your computer and do not contain viruses. The purpose of cookies is to make our website more user friendly, effective and more secure. Cookies are small text files that are placed on your computer and stored by your browser.</p> <p>Most of the cookies we use are so-called “session cookies.” They are automatically deleted after your leave our site. Other cookies will remain archived on your device until you delete them. These cookies enable us to recognise your browser the next time you visit our website.</p> <p>You can adjust the settings of your browser to make sure that you are notified every time cookies are placed and to enable you to accept cookies only in specific cases or to exclude the acceptance of cookies for specific situations or in general and to activate the automatic deletion of cookies when you close your browser. If you deactivate cookies, the functions of this website may be limited.</p> <p>Cookies that are required for the performance of the electronic communications transaction or to provide certain functions you want to use (e.g. the shopping cart function), are stored on the basis of Art. 6 Sect. 1 lit. f GDPR. The website operator has a legitimate interest in storing cookies to ensure the technically error free and optimised provision of the operator’s services. If other cookies (e.g. cookies for the analysis of your browsing patterns) should be stored, they are addressed separately in this Data Protection Declaration.</p><h4>4. Analysis tools and advertising</h4><h5>1&1 Web Analytics</h5> <p>This website uses 1&1-Webanalytics analysis services. The provider of these services is 1&1 Internet SE, Elgendorfer Straße 57, 56410 Montabaur, Germany. In conjunction with the performance of analyses by 1&1, it is possible to e.g. analyze the number of visitors and their behavior patterns during visits (e.g. number of pages accessed, duration of their visits to the website, percentage of aborted visits), visitor origins (i.e. from which site does the visitor arrive at our site), visitor locations as well as technical data (browser and session of operating system used). For these purposes, 1&1 archives in particular the following data:</p> <ul> <li>Referrer (previously visited website)</li> <li>Accessed page on the website or file</li> <li>Browser type and browser version</li> <li>Used operating system</li> <li>Type of device used</li> <li>Website access time</li> <li>Anonymized IP address (used only to determine the access location)</li> </ul> <p>According to 1&1, the data recorded are completely anonymized so they cannot be tracked back to individuals. 1&1-Webanalytics does not archive cookies.</p> <p>The data are stored and analyzed pursuant to Art. 6 Sect. 1 lit. f GDPR. The operator of the website has a legitimate interest in the statistical analysis of user patterns to optimize both, the operator’s web presentation as well as the operator’s promotional activities. For more information affiliated with the recording and processing of data by 1&1-Webanalytics, please click on the following links:</p> <p><a href="https://hosting.1und1.de/hilfe/online-marketing/" target="_blank" rel="noopener">https://hosting.1und1.de/hilfe/online-marketing/</a><br /> <a href="https://hosting.1und1.de/hilfe/datenschutz/datenverarbeitung-von-webseitenbesuchern-ihres-company-name-produktes/webanalytics/" target="_blank" rel="noopener">https://hosting.1und1.de/hilfe/datenschutz/datenverarbeitung-von-webseitenbesuchern-ihres-company-name-produktes/webanalytics/</a><br /> <a href="https://hosting.1und1.de/terms-gtc/terms-privacy/" target="_blank" rel="noopener">https://hosting.1und1.de/terms-gtc/terms-privacy/</a>.</p><h4>5. Plug-ins and Tools</h4><h5>Google Web Fonts</h5> <p>To ensure that fonts used on this website are uniform, this website uses so-called Web Fonts provided by Google. When you access a page on our website, your browser will load the required web fonts into your browser cache to correctly display text and fonts.</p> <p>To do this, the browser you use will have to establish a connection with Google’s servers. As a result, Google will learn that your IP address was used to access our website. The use of Google Web Fonts is based on our interest in presenting our online content in a uniform and appealing way. According to Art. 6 Sect. 1 lit. f GDPR, this is a legitimate interest.</p> <p>If your browser should not support Web Fonts, a standard font installed on your computer will be used.</p> <p>For more information on Google Web Fonts, please follow this link: <a href="https://developers.google.com/fonts/faq" target="_blank" rel="noopener">https://developers.google.com/fonts/faq</a> and consult Google’s Data Privacy Declaration under: <a href="https://policies.google.com/privacy?hl=en" target="_blank" rel="noopener">https://policies.google.com/privacy?hl=en</a>.</p>',this.props.lang))}




                    </div>

                  </div>
                </div>
                <Footer setPath={this.props.setPath} lang ={this.props.lang} path={this.props.path}></Footer>

            </div>

        )
    }

}